*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html, body{
  background: #102569;
  color: white;
  font-size: 16px;
  width: 100%;
  height: 100%;
}
.App{
  width: 100%;
  height: 100%;
}
/* SCROLL BAR */
::-webkit-scrollbar {
  width: 14px;
}
/* Track */
::-webkit-scrollbar-track {
  background: #0c1d53;
  border-radius: 10px;
}
/* Handle */
::-webkit-scrollbar-thumb {
  background: #2950d4; 
  border-radius: 10px;
}
/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #3564ff; 
}
/* HOME */
.home-container{
  width: 100%;
  height: 100%;
}
/* HEADER */
header{
  position: relative;
  display: flex;
  justify-content: space-between;
  background: #0c1d53;
  padding: 15px 25px;
  align-items: center;
  position: fixed;
  left: 0;
  width: 100%;
  z-index: 9999;
}
.logo-box{
  width: 60px;
  height: 60px;
  border-radius: 50%;
  overflow: hidden;
}
.logo-box img{
  width: 100%;
  height: 100%;
  display: block;
}
.header-nav{
  display: flex;
}
.header-nav li{
  list-style: none;
  margin-left: 110px;
  font-size: 24px;
  text-transform: uppercase;
  font-weight: 600;
  cursor: pointer;
}
.header-nav li a{
  color: white;
  text-decoration: none;
}
.right-menu{
  display: none;
}
.right-menu i{
  cursor: pointer;
}
.right-menu ul {
  position: absolute;
  right: 0;
  background: #0c1d53;
  height: 100vh;
  padding: 15px 30px;
  top: 0;
  padding-top: 100px;
  z-index: -1;
  display: none;
}
.right-menu ul li{
  list-style: none;
  margin-bottom: 15px;
}
.right-menu ul li a{
  color: white;
  text-decoration: none;
}
.active{
  display: block !important;
}
.banner-box{
  position: relative;
}
.banner-box h1{
  font-family: 'Warnes', cursive;
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 99;
  transform: translateX(-50%) translateY(-50%);
  font-size: 80px;
  text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 15px #0073e6, 0 0 20px #0073e6, 0 0 25px #0073e6, 0 0 30px #0073e6, 0 0 35px #0073e6;
  width: fit-content;
  font-weight: lighter;
  animation: ani1 1s infinite;
}
@keyframes ani1{
  from{
    text-shadow: 0 0 3px #fff, 0 0 10px #fff, 0 0 15px #0073e6, 0 0 20px #0073e6, 0 0 25px #0073e6, 0 0 30px #0073e6, 0 0 35px #0073e6;
  }
  to{
    text-shadow: 0 0 10px #fff, 0 0 10px #fff, 0 0 15px #0073e6, 0 0 20px #0073e6, 0 0 25px #0073e6, 0 0 30px #0073e6, 0 0 35px #0073e6;
  }
}
.banner-box img{
  width: 100%;
  height: 100%;
  filter: brightness(0.7);
  z-index: 1;
}
/* LOADING */
.loading-container{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 700px;
}
.loading-container img{
  width: 90px;
}
/* BUTTON */
.btn-container{
  margin: 0 50px;
  width: fit-content;
  display: flex;
}
.btn{
  padding: 10px 15px;
  font-size: 19px;
  border: 2px solid #3968FF;
  border-radius: 35px;
  text-transform: uppercase;
  background: none;
  color: white;
  margin: 0 10px;
  transition: all .2s;
  outline: none;
  cursor: pointer;
}
.btn-active{
  background: #3968ff;
}
.btn:hover{
  background: #3968FF;
}
/* FILM */
.film-container{
  padding: 35px 45px;
  position: relative;
}
.film-container h1{
  text-transform: uppercase;
  font-size: 40px;
  text-shadow: 0px 0px 6px rgb(255 255 255 / 70%);
}
.film-bottom{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.btn-container{
  padding: 15px 0 15px 25px;
}
.list-film-box{
  width: 1200px;
  margin: 0 auto;
  overflow: hidden;
}
.list-film-container{
  padding: 30px 0;
  display: flex;
  justify-content: space-between;
  
}
.film-box{
  text-align: center;
  background: #1D3892;
  border-radius: 10px;
  overflow: hidden;
  cursor: pointer;
  min-width: 290px;
  max-width: 290px;
  margin: 0 5px;
}
.film-box-img{
  width: 290px;
  overflow: hidden;
}
.film-box-img img{
  width: 100%;
  height: 100%;
  display: block;
  transition: all .3s;
}
.film-box:hover .film-box-img img{
  transform: scale(1.1);
}
.film-box-title{
  text-transform: uppercase;
  padding: 30px 0;
  font-size: 25px;
}
.btn-direct i{
  font-size: 45px;
  cursor: pointer;
}
/* EVENT */
.event-container{
  padding: 0 45px;
}
.event-container h1{
  text-transform: uppercase;
  font-size: 40px;
  text-shadow: 0px 0px 6px rgb(255 255 255 / 70%);
}
.event-box{
  width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
 padding: 15px 0;
}
.event{
  overflow: hidden;
  width: calc(50% - 15px);
  border-radius: 10px;
}
.event img{
  width: 100%;
  height: 100%;
  display: block;
}
/* FOOTER */
footer{
  padding: 50px 25px 15px 25px;
  
}
.footer-top{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 15px;
}
.footer-nav{
  display: flex;
}
.footer-nav li{
  list-style: none;
    margin-left: 25px;
    background: #3968FF;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: all .2s;
}
.footer-nav li:hover{
  background: none;
}
.footer-nav li a{
  text-decoration: none;
  font-size: 19px;
}
.footer-bottom{
  padding-top: 15px;
  border-top: 2px solid #3968ff;
}
.footer-bottom span{
  color: #d0d0d0;
  font-weight: lighter;
}
/* FILM_SCHEDULE */
.film-schedule{
  padding-top: 100px;
}
/* FILM-CONTENT */
.film-content{
  margin: 30px 60px;
  padding: 30px;
  background: #22409E;
  border-radius: 15px;
}
.film-content-top{
