  line-height: 35px;
  font-weight: lighter;
  font-size: 20px;
}
.film-content-main span h4{
  margin-right: 10px;
  font-size: 19px;
}
.film-classify{
  display: flex;
  align-items: center;
  margin-top: 10px;
}
.film-classify-box{
